.moviesList .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    align-items: flex-start;
    justify-content: space-between;
    display: flex;
    font-weight: 300;
    padding: 20px;
    color: #999eaf;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(50, 50, 93, 0.25);
    border-radius: 5px;
    min-width: 500px;
}

.movie img {
    top: -50px;
    position: relative;
    max-width: 160px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(50, 50, 93, 0.25);
}

.movie__info__title,
.movie__info__year {
    margin: 0px;
    font-weight: 300;

}

.movie__info__year{
    font-size: 12px;
}

.movie__info__title {
    margin-bottom: 10px;
    font-size: 35px;
    color: #2c2c2c;
    display: inline;
    margin-right: 10px;
}

.movie__info__genres {
    list-style: none;
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-wrap: wrap;
    font-size: 18px;
} 

.movie__info__genres li {
    margin-right: 10px;
}
